import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { getTrends } from '../actions/post.actions'
import { isEmpty } from './Utils'
import { NavLink } from 'react-router-dom'

const Trends = () => {
  const posts = useSelector((state) => state.allPostsReducer)

  // On appelle le store "usersReducer", car on aura besoin de rcuprer dynamiquement la photo de profil de l'utilisateur.
  const usersData = useSelector((state) => state.usersReducer)

  // Store des tendances.
  const trendList = useSelector((state) => state.trendingReducer)

  const dispatch = useDispatch()

  // On calcule les posts les plus aims.
  useEffect(() => {
    if (!isEmpty(posts[0])) {
      // On cre tout d'abord un tableau pour enregistrer chaque post par "cl", pour ensuite appeller la mthode JavaScript "sort()" pour trier les posts par nombre de "j'aime", du plus aim au moints aim.
      const postsArr = Object.keys(posts).map((i) => posts[i])

      let sortedArray = postsArr.sort((a, b) => {
        return b.likers.length - a.likers.length
      })

      // On garde les 3 posts les plus aims pour les afficher dans la section ddie sur la page d'accueil.
      sortedArray.length = 3

      dispatch(getTrends(sortedArray))
    }
  }, [posts, dispatch])

  return (
    <div className="trending-container">
      <h4>Trending</h4>
      <NavLink exact to="/trending">
        <ul>
          {trendList.length &&
            trendList.map((post) => {
              return (
                <li key={post.id}>
                  <div>
                    {/* On rcupre la photo associe au post s'il y en a une */}
                    {post.picture && <img src={post.picture} alt="post-pic" />}

                    {/* On rcupre la vido associe au post s'il y en a une */}
                    {post.video && (
                      <iframe
                        title={post._id}
                        width="500"
                        height="300"
                        src={post.video}
                        frameBorder="0"
                        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                        allowFullScreen
                      ></iframe>
                    )}

                    {/* On rcupre la photo de profil de l'auteur du post, si aucune image ou vido n'est associe  son post */}
                    {isEmpty(post.picture) && isEmpty(post.video) && (
                      <img
                        src={
                          usersData[0] &&
                          usersData
                            .map((user) => {
                              if (user._id === post.posterId) {
                                return user.picture
                              } else return null
                            })
                            .join('')
                        }
                        alt="profile-pic"
                      />
                    )}
                  </div>

                  <div className="trend-content">
                    {/* On affiche le message contenu dans le post en tendance */}
                    <p>{post.message}</p>
                    <span>Lire</span>
                  </div>
                </li>
              )
            })}
        </ul>
      </NavLink>
    </div>
  )
}

export default Trends
