import React from 'react'
import ReactDOM from 'react-dom'
import App from './App'
import './styles/index.scss'
import { Provider } from 'react-redux'
import { applyMiddleware, createStore } from 'redux'
import { getUsers } from './actions/users.actions'
import { getPosts } from './actions/post.actions'

// "Thunk" est un middleware nous permettant de faire des requtes asynchrones avec Redux.
import thunk from 'redux-thunk'

// On a juste  appeler le dossier, vu qu'il contient un fichier nomm "index.js".
import rootReducer from './reducers'

/** Dev-tools */
// Le paquet "redux-devtools-extension" nous permet de travailler avec le plugin de navigateur ponyme.
import { composeWithDevTools } from 'redux-devtools-extension'

const store = createStore(
  // Avec la fonction composeWithDevTools, on autorise notre navigateur  accder au store de notre application via le plugin navigateur 'redux-devtools-extension'. CETTE FONCTION, AINSI QUE LES FONCTIONS DE LOG ET DE DBUG DE REDUX, NE DOIVENT JAMAIS TRE PRSENTES EN PRODUCTION !!!
  // On regroupe tous nos reducers dans le rootReducer pour qu'il s'incremente avec eux
  rootReducer,
  composeWithDevTools(applyMiddleware(thunk))
)

// On doit appeler la fonction getUsers (obtenir la liste des abonnements de l'utilisateur) LE PLUS RAPIDEMENT POSSIBLE, C--D ds que l'utilisateur ouvre une page et voit son fil d'actualits s'afficher sur la page d'accueil. On doit donc tre en mesure d'afficher la photo de profil des autres utilisateurs, leurs pseudos, leurs commentaires.
// Ds qu'on lance notre application, on effectue une requte GET pour envoyer tous les utilisateurs, pour qu'on puisse traiter ces informations pour les afficher.
store.dispatch(getUsers())

// On en fait de mme avec tous les posts du site pour afficher les posts en tendance dans la section ddie.
store.dispatch(getPosts())

// On injecte ici (dans <App />) tous les affichages et toute la logique de l'application.
// La mthode "document.getElementById('root')" fait rfrence  l'ID "root" dans le fichier "index.html"

/** la variable "store" appele dans l'attribut "store" est la constante dfinie ci-dessus */
ReactDOM.render(
  <Provider store={store}>
    {/* On englobe toute notre application dans le component provider */}
    <App />
  </Provider>,
  document.getElementById('root')
)

//
