import axios from 'axios'

// Ici, c'est une table des matires, o toutes nos actions sont regroupes.

// Quand on appelera "GET_USER" au niveau du reducer, il y  aura une logique  mettre.
export const GET_USER = 'GET_USER'

export const UPLOAD_PICTURE = 'UPLOAD_PICTURE'

export const UPDATE_BIO = 'UPDATE_BIO'

export const FOLLOW_USER = 'FOLLOW_USER'

export const UNFOLLOW_USER = 'UNFOLLOW_USER'

export const GET_USER_ERRORS = 'GET_USER_ERRORS'

// Paramtre : l'user ID de notre utilisateur.
export const getUser = (uid) => {
  // Le dispatch est la donne qui sera envoye au reducer pour qu'il la mette dans le store. Avant d'envoyer les choses au reducer, on envoie les donnes  la BDD.
  return (dispatch) => {
    // On veut rcuprer les infos de notre utilisateur.
    return axios
      .get(`${process.env.REACT_APP_API_URL}api/user/${uid}`)
      .then((res) => {
        // Ici, on evoie des infos au reducer.
        dispatch({
          type: GET_USER,
          // Toute la data recherche dans la base donnes est passe au reducer via le payload, qui va envoyer ces donnes au store.
          payload: res.data,
        })
      })
      .catch((err) => console.log(err))
  }
}

export const uploadPicture = (data, id) => {
  return (dispatch) => {
    // On envoie  notre back toutes ces infos, il va crer un nouveau nom d'image.
    return (
      axios
        // On envoie d'abord ces infos  la BDD (on cre un nom de fichier, on stocke l'image dans le dossier "public/uploads/profil")
        .post(`${process.env.REACT_APP_API_URL}api/user/upload`, data)
        .then((res) => {
          // On vrifie la compatibilit du fichier envoy.
          if (res.data.errors) {
            dispatch({ type: GET_USER_ERRORS, payload: res.data.errors })
          } else {
            // Si une erreur a eu lieu prcdemment, on supprime le message d'erreur prcdemment affich.
            dispatch({ type: GET_USER_ERRORS, payload: '' })

            // On effectue une requte GET pour chercher les informations de l'image, car on ne connat ni le nom de la sauvegarde, ni le chemin. On demande  la BDD de nous fournir ce qu'elle a enregistr.
            return (
              axios
                // Ici, aprs avoir envoy les infos  la BDD, on passe le chemin de l'image...
                .get(`${process.env.REACT_APP_API_URL}api/user/${id}`)
                .then((res) => {
                  // Puis on rcupre le chemin de cette image pour l'enregistrer dans le store, pour que l'utilisateur sache que son image de profil a t actualise.
                  dispatch({ type: UPLOAD_PICTURE, payload: res.data.picture })
                })
                .catch((err) => console.log(err))
            )
          }
        })
        .catch((err) => console.log(err))
    )
  }
}

// Action -> lment qui va envoyer les infos  la BDD sans rien lui demander en retour,
// Reducer -> montre visuellement  l'utilisateur cd qui a volu dans le store.
// userId : Pour identiifer l'utilisateur dans la requte.
export const updateBio = (userId, bio) => {
  return (dispatch) => {
    // Cette faon de transfrer les donnes  la BDD marche aussi.
    return axios({
      method: 'put',
      url: `${process.env.REACT_APP_API_URL}api/user/` + userId,
      data: { bio },
    })
      .then((res) => {
        dispatch({ type: UPDATE_BIO, payload: bio })
      })
      .catch((err) => console.log(err))
  }
}

// Paramtres : ID d'utilisateur, ID de la personne  suivre.
export const followUser = (followerId, idToFollow) => {
  return (dispatch) => {
    return axios({
      method: 'patch',
      url: `${process.env.REACT_APP_API_URL}api/user/follow/` + followerId,
      data: { idToFollow },
    })
      .then((res) => {
        dispatch({
          type: FOLLOW_USER,
          payload: { idToFollow },
        })
      })
      .catch((err) => console.log(err))
  }
}

// Paramtres : ID d'utilisateur, ID de la personne  ne plus suivre.
export const unfollowUser = (followerId, idToUnfollow) => {
  return (dispatch) => {
    return axios({
      method: 'patch',
      url: `${process.env.REACT_APP_API_URL}api/user/unfollow/` + followerId,
      data: { idToUnfollow },
    })
      .then((res) => {
        dispatch({
          type: UNFOLLOW_USER,
          payload: { idToUnfollow },
        })
      })
      .catch((err) => console.log(err))
  }
}
