import React, { useState } from 'react'
import LeftNav from '../LeftNav'
import { useDispatch, useSelector } from 'react-redux'
import UploadImg from './UploadImg'
import { updateBio } from '../../actions/user.actions'
import { dateParser } from '../Utils'
import FollowHandler from './FollowHandler'

const UpdateProfil = () => {
  const [bio, setBio] = useState('')

  // Formulaire de mise  jour de la bio. Tant que l'utilisateur ne clique pas dessus, son tat est  "false", mais il passe  "true" dans le cas contraire, et l il devient possible d'diter la bio.
  const [updateForm, setupdateForm] = useState(false)
  const userData = useSelector((state) => state.userReducer)
  const usersData = useSelector((state) => state.usersReducer)
  const error = useSelector((state) => state.errorReducer.userError)

  const dispatch = useDispatch()

  // On met l'tat de ces popups  "false", car on ne veut les afficher que quand l'utilisatuer clique sur l'un des deux boutons "abonnements" et "abonns".
  const [followingPopup, setFollowingPopup] = useState(false)
  const [followersPopup, setFollowersPopup] = useState(false)

  const handleUpdate = () => {
    dispatch(updateBio(userData._id, bio))

    // Maintenant, il faut que, visuellement, notre formulaire disparaisse pour que la case de la bio redevienne comme avant. Pour cel, on remet l'tat "updateForme"  false.
    setupdateForm(false)
  }

  return (
    <div className="profile-container">
      {/* On cre la barre de navigation en premier */}
      <LeftNav />
      {/* Le pseudo qui s'affiche galement dans la barre de navigation suprieure vient d'un autre component, mais grce au store, on arrive  utiliser la donne indpendemment des components */}
      <h1>Profil de {userData.pseudo}</h1>
      <div className="update-container">
        <div className="left-part">
          <h3>Votre photo de profil</h3>
          <img src={userData.picture} alt="user-pic" />
          <UploadImg />
          {/* Affichage des erreurs en cas d'envoi d'un format incompatible */}
          <p>{error.maxSize}</p>
          <p>{error.format}</p>
        </div>
        <div className="right-part">
          <div className="bio-update">
            <h3>Bio</h3>
            {/* Rendu conditionnel (on met un fragment, car il y a deux choses au mme niveau de l'application) */}
            {updateForm === false && (
              <>
                {/* Dans le onClick, on lance la partie de mise  jour de la bio, et "updateForm" devient vrai (elle est de base sur "false") */}
                {/* Dans la balise 'p', on retourne le contenu de la bio, qui est enregistr dans le store. Lorsqu'on clique sur le bouton, on met l'tat d'updateForm  "true". */}
                <p onClick={() => setupdateForm(!updateForm)}>{userData.bio}</p>
                <button onClick={() => setupdateForm(!updateForm)}>
                  Modifier la bio
                </button>
              </>
            )}

            {updateForm === true && (
              <>
                {/* defaultValue : Si l'utilisateur a dj crit une bio, il faut qu'elle soit affiche, pour qu'nesuite il puisse voir quelle partie il souhaite diter */}
                {/* onChange : ds que la bio est modifie, on affiche le nouveau contenu  l'cran */}
                <textarea
                  type="text"
                  defaultValue={userData.bio}
                  onChange={(e) => setBio(e.target.value)}
                ></textarea>
                <button onClick={handleUpdate}>
                  Valider les modifications
                </button>
              </>
            )}
          </div>
          <h4>Membre depuis le : {dateParser(userData.createdAt)}</h4>
          {/* Il faut imprativement qu'une donne soit prsente dans le champ "following", sinon appeler la proprit "userData.following.length" nous renverra une erreur. On peut aussi y mettre une chane de caractres vide au lieu d'un '0', le '0' sera tout de mme affich */}
          <h5 onClick={() => setFollowingPopup(true)}>
            Abonnements : {userData.following ? userData.following.length : ''}
          </h5>
          <h5 onClick={() => setFollowersPopup(true)}>
            Abonns : {userData.followers ? userData.followers.length : ''}
          </h5>
        </div>
      </div>

      {/* Logique des popups */}
      {followingPopup && (
        <div className="popup-profil-container">
          <div className="modal">
            <h3>Abonnements</h3>
            {/* On cre une croix avec le code HTML "&#10005;" qui va nous permettre de fermer la popup en mettant l'tat "setFollowingPopup" sur false */}
            <span className="cross" onClick={() => setFollowingPopup(false)}>
              &#10005;
            </span>
            {/* Liste des abonnements de l'utilisateur. Avec la mthode "map()", on rcupre la liste des utilisateurs du site. */}
            <ul>
              {usersData.map((user) => {
                // Pour chaque abonnement de l'utilisateur (chacun a un ID). Chaque abonn sera ajout un par un  la liste des abonns via le return.
                for (let i = 0; i < userData.following.length; i++) {
                  // Si un utilisateur du site apparat parmi les gens suivis par l'utilisateur actuel. La variable 'i' correspond  l'index actuellement trait du tableau d'utilisateurs du site dont l'utilisateur actuel est abonn.
                  if (user._id === userData.following[i]) {
                    // On retourne les infos de l'abonn actuellement trait.
                    // Comme il s'agit d'un map, il faut une cl.
                    return (
                      <li key={user._id}>
                        {/* On envoie dans l'attribut "src" le chemin de l'image de profil de l'abonn. */}
                        <img src={user.picture} alt="user-pic" />
                        <h4>{user.pseudo}</h4>
                        <div className="follow-handler">
                          {/* On ajoute la possibilit de ne plus suivre l'utilisateur. On passe l'ID de l'abonn dans le prop "idToFollow" */}
                          <FollowHandler
                            idToFollow={user._id}
                            type={'suggestion'}
                          />
                        </div>
                      </li>
                    )
                  }
                }
                return null
              })}
            </ul>
          </div>
        </div>
      )}

      {followersPopup && (
        <div className="popup-profil-container">
          <div className="modal">
            <h3>Abonns</h3>
            {/* On cre une croix avec le code HTML "&#10005;" qui va nous permettre de fermer la popup en mettant l'tat "setFollowingPopup" sur false */}
            <span className="cross" onClick={() => setFollowersPopup(false)}>
              &#10005;
            </span>
            {/* Liste des abonnements de l'utilisateur. Avec la mthode "map()", on rcupre la liste des utilisateurs du site. */}
            <ul>
              {usersData.map((user) => {
                // Pour chaque abonnement de l'utilisateur (chacun a un ID). Chaque abonn sera ajout un par un  la liste des abonns via le return.
                for (let i = 0; i < userData.followers.length; i++) {
                  // Si un utilisateur du site apparat parmi les gens suivis par l'utilisateur actuel. La variable 'i' correspond  l'index actuellement trait du tableau d'utilisateurs du site dont l'utilisateur actuel est abonn.
                  if (user._id === userData.followers[i]) {
                    // On retourne les infos de l'abonn actuellement trait.
                    // Comme il s'agit d'un map, il faut une cl.
                    return (
                      <li key={user._id}>
                        {/* On envoie dans l'attribut "src" le chemin de l'image de profil de l'abonn. */}
                        <img src={user.picture} alt="user-pic" />
                        <h4>{user.pseudo}</h4>
                        <div className="follow-handler">
                          {/* On ajoute la possibilit de suivre l'utilisateur. */}
                          <FollowHandler
                            idToFollow={user._id}
                            type={'suggestion'}
                          />
                        </div>
                      </li>
                    )
                  }
                }
                return null
              })}
            </ul>
          </div>
        </div>
      )}
    </div>
  )
}

export default UpdateProfil
