import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { followUser, unfollowUser } from '../../actions/user.actions'
import { isEmpty } from '../Utils'

// Les arguments de ce component sont ses props.
// Le type : Si c'est le type "suggestion" comme dans le panneau "Suggestion" de la page d'accueil, on affiche le bouton avec le texte "Suivre". Sinon, si c'est le type "card", qu'on voit  ct du pseudo de l'auteur d'un post, alors on affiche l'icne avec le coche.
const FollowHandler = ({ idToFollow, type }) => {
  const userData = useSelector((state) => state.userReducer)

  // On dtermine si la personne affiche est suivie ou non par l'utilisateur actuel. Par dfaut, le modal est sur "false", car de base, on considrera toujours que la personne  suivre n'est pas suivie par l'utilisateur. Quand on appelera le modal, on frea donc un petit calcul qui dterminera si un autre utilisateur est suivi ou pas par notre utilisateur.
  const [isFollowed, setIsFollowed] = useState(false)

  const dispatch = useDispatch()

  const handleFollow = () => {
    dispatch(followUser(userData._id, idToFollow))

    // Une fois l'utilisateur suivi, on met le modal "setIsFollowed" sur "true".
    setIsFollowed(true)
  }

  const handleUnfollow = () => {
    // On passe en paramtre "idToFollow", car ce sera tout de mme l'ID de la mme personne actuellement suivie.
    dispatch(unfollowUser(userData._id, idToFollow))
    setIsFollowed(false)
  }

  useEffect(
    () => {
      // On vrifie si le tableau d'abonnements de l'utilisateur n'est vide, grce  la fonction "isEmpty" que nous avons cod dans le fichier "Utils.js".
      if (!isEmpty(userData.following)) {
        // On vrifie si l'utilisateur actuel suit dj l'utilisateur cible.
        if (userData.following.includes(idToFollow)) {
          // Vu que la valeur est sur true (l'utilisateur cible est suivi par l'utilisateur actuel), le bouton ne marquera plus "suivre", mais "se dsabonner".
          setIsFollowed(true)
        } else {
          setIsFollowed(false)
        }
      }
    },
    // Le principe de ce callback est de relancer le useEffect quand le userData evolue. Il volue quand un autre utilisateur est ajout ou supprim de la liste des abonnements de l'utilisateur actuel.
    [userData, idToFollow]
  )

  return (
    <>
      {isFollowed && !isEmpty(userData) && (
        <span onClick={handleUnfollow}>
          {type === 'suggestion' && (
            <button className="unfollow-btn">Abonn</button>
          )}
          {type === 'card' && (
            <img src="./img/icons/checked.svg" alt="checked" />
          )}
        </span>
      )}
      {isFollowed === false && !isEmpty(userData) && (
        <span onClick={handleFollow}>
          {type === 'suggestion' && (
            <button className="follow-btn">Suivre</button>
          )}
          {type === 'card' && <img src="./img/icons/check.svg" alt="check" />}
        </span>
      )}
    </>
  )
}

export default FollowHandler
