import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { dateParser, isEmpty } from '../Utils'
import FollowHandler from '../Profil/FollowHandler'
import LikeButton from './LikeButton'
import { updatePost } from '../../actions/post.actions'
import DeleteCard from './DeleteCard'
import CardComments from './CardComments'

// Le "post" est le prop pass lors de l'appel du component "Card" dans le fichier "Thread.js".
const Card = ({ post }) => {
  // Tant qu'on n'a pas les donnes compltes pour afficher le pont, on montre  l'utilisateur que le post charge.
  const [isLoading, setIsLoading] = useState(true)

  // Mise  jour d'un post de par l'auteur du post lui-mme.
  const [isUpdated, setIsUpdated] = useState(false)
  const [textUpdate, setTextUpdate] = useState(null)

  // Affichage des commentaires (de base, on ne les montre pas).
  const [showComments, setShowComments] = useState(false)

  // Il faut qu'on ait toute la BDD de nos utilisateurs pour savoir ce qu'ils ont aims, quel est le nom de l'auteur du post, sa photo, car ce ne sont pas des choses qui sont stockes dans le store.
  const usersData = useSelector((state) => state.usersReducer)

  // On rcupre les donnes de notre utilisateur.
  const userData = useSelector((state) => state.userReducer)

  const dispatch = useDispatch()

  const updateItem = () => {
    // S'il y a quelque chose qui a chang dans le message  diter (sinon a ne sert  rien de faire un requte dans la BDD)
    if (textUpdate) {
      dispatch(updatePost(post._id, textUpdate))
    }

    setIsUpdated(false)
  }

  useEffect(() => {
    // Si on a les donnes du post, on remet le modal "isLoading" sur "false".
    !isEmpty(usersData[0]) && setIsLoading(false)
  }, [usersData])

  return (
    <li className="card-container" key={postMessage._id}>
      {isLoading ? (
        <i className="fas fa-spinner fa-spin"></i>
      ) : (
        <>
          {/* On affiche dans la partie de gauche du post la photo de profil de l'utilisateur qui a post le post  charger. */}
          <div className="card-left">
            {/* Si jamais on rclame une donne qui n'a pas t cherche dans la BDD, React n'attendra pas de donnes et renverra une erreur. Avant qu'il ne le fasse, il faut lui dire que l'on a les donnes */}
            <img
              src={
                !isEmpty(usersData[0]) &&
                // On doit mapper, car on ne peut pas sauvegarder la photo de l'auteur du post au moment o il poste, car entre temps il peut trs bien changer de photo, et le post continuerait d'afficher son ancienne photo.
                usersData
                  .map((user) => {
                    // Si on trouve l'ID de l'utilisateur qui a post le post, on retourne le chemin vers sa photo de profil,
                    if (user._id === post.posterId) return user.picture
                    else return null
                  })
                  // La mthode "join()" permet d'viter que la mthode "map()" ne mette des virgules entre chaque lment dans l'attribut "src" de la balise "img", comme on peut le voir dans l'inspecteur. On met donc une string vide pour joindre chaque lment.
                  .join('')
              }
              alt="poster-pic"
            />
          </div>

          <div className="card-right">
            <div className="card-header">
              <div className="pseudo">
                <h3>
                  {!isEmpty(usersData[0]) &&
                    // On doit mapper, car on ne peut pas sauvegarder le pseudo de l'auteur du post au moment o il poste, car entre temps il peut trs bien changer de pseudo, et le post continuerait d'afficher son ancien pseudo.
                    usersData.map((user) => {
                      // Si on trouve l'ID de l'utilisateur qui a post le post, on retourne son pseudo,
                      if (user._id === post.posterId) return user.pseudo
                      else return null
                    })}
                </h3>
                {/* On code une condition pour empcher l'utilisateur actuel d'tre ami avec lui-mme */}
                {/* Si l'ID du posteur n'est pas celui de l'utilisateur */}
                {post.posterId !== userData._id && (
                  <FollowHandler idToFollow={post.posterId} type={'card'} />
                )}
              </div>
              {/* On affiche la date du post */}
              <span>{dateParser(post.createdAt)}</span>
            </div>
            {isUpdated === false && <p>{post.message}</p>}
            {isUpdated && (
              <div className="update-post">
                {/* Lorsqu'on clique sur le message  diter, on souhaite conserver le message original pour que l'utilisateur puisse le modifier*/}
                <textarea
                  defaultValue={post.message}
                  onChange={(e) => setTextUpdate(e.target.value)}
                />
                <div className="button-container">
                  <button className="btn" onClick={updateItem}>
                    Valider les modifications
                  </button>
                </div>
              </div>
            )}
            {/* Si une photo est attache au post, alors on l'affiche (comme dit prcdemment, si jamais on rclame une donne qui n'a pas t cherche ou trouve dans la BDD, React n'attendra pas de donnes et renverra une erreur. Avant qu'il ne le fasse, il faut lui dire que l'on a les donnes) */}
            {/* Dans le "src", on passe le chemin de la photo attache  ce post. */}
            {post.picture && (
              <img src={post.picture} alt="card-pic" className="card-pic" />
            )}
            {/* Mme chose que prcdemment, avec les vidos postes */}
            {post.video && (
              <iframe
                title={post._id}
                width="500"
                height="300"
                src={post.video}
                frameBorder="0"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                allowFullScreen
              ></iframe>
            )}
            {/* On propose  l'utilisateur la possibilit d'diter ou de supprimer SON PROPRE message ici */}
            {userData._id === post.posterId && (
              <div className="button-container">
                {/* Au lieu de mettre imprativement l'tat du modal "isUpdated" sur "true", on met la valuer inverse pour activer ou dsactiver le mode d'dition en y refaisant un clic dessus */}
                <div onClick={() => setIsUpdated(!isUpdated)}>
                  <img src="./img/icons/edit.svg" alt="edit" />
                </div>
                <DeleteCard id={post._id} />
              </div>
            )}
            <div className="card-footer">
              <div className="comment-icon">
                <img
                  onClick={() => setShowComments(!showComments)}
                  src="./img/icons/message1.svg"
                  alt="comment"
                />
                {/* Obtention du nombre de commentaires obtenus */}
                <span>{post.comments.length}</span>
              </div>
              <LikeButton post={post} />
              <img src="./img/icons/share.svg" alt="share" />
            </div>
            {/* Si l'utilisateur a dcid de voir les commentaires sous un post, alors on appelle le component CardComment, pour dclencher toute la logique derrire le component */}
            {showComments && <CardComments post={post} />}
          </div>
        </>
      )}
    </li>
  )
}

export default Card
