import React, { useState } from 'react'
import axios from 'axios'
import SignInForm from './SingInForm'

const SignUpForm = () => {
  const [formSubmit, setFormSubmit] = useState(false)
  const [pseudo, setPseudo] = useState('')
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [controlPassword, setControlPassword] = useState('')

  // Prise en compte de l'inscription de l'utilisateur.
  const handleRegister = async (e) => {
    e.preventDefault()

    // On rcupre l'ID de la checkbox de l'acceptation des CGU.
    const terms = document.getElementById('terms')
    const pseudoError = document.querySelector('.pseudo.error')
    const emailError = document.querySelector('.email.error')
    const passwordError = document.querySelector('.password.error')
    const passwordConfirmError = document.querySelector(
      '.password-confirm.error'
    )
    const termsError = document.querySelector('.terms.error')

    //  chaque fois que l'on relance le formulaire, il faut supprimer le texte inject via la proprit "innerHTML".
    passwordConfirmError.innerHTML = ''
    termsError.innerHTML = ''

    // Si les mots de passe ne correspondent pas ou si la case des CGU n'est pas coche, on affiche le message d'erreur correspondant, les deux en mme temps si les deux conditions ne sont pas valides.
    if (password !== controlPassword || !terms.checked) {
      if (password !== controlPassword)
        passwordConfirmError.innerHTML =
          'Les mots de passe ne correspondent pas'

      if (!terms.checked)
        termsError.innerHTML =
          "Veuillez valider les conditions gnrales d'utilisation"

      // Sinon, si aucune erreur n'a t dtecte, on traite les donnes du formulaire envoyes au serveur.
    } else {
      await axios({
        method: 'post',
        url: `${process.env.REACT_APP_API_URL}api/user/register`,

        // On passe les donnes requises de notre API  cette dernire.
        data: {
          pseudo,
          email,
          password,
        },
      })
        .then((res) => {
          console.log(res)

          // Si une ou des erreurs sont dtectes, seule la chane de caractres d'erreur de la donne errone est affiche.
          if (res.data.errors) {
            pseudoError.innerHTML = res.data.errors.pseudo
            emailError.innerHTML = res.data.errors.email
            passwordError.innerHTML = res.data.errors.password

            // Si aucune erreur n'est dtecte, on passe l'tat "setFormSubmit" sur "true" pour afficher la page d'inscription  l'utilisateur.
          } else {
            setFormSubmit(true)
          }
        })
        .catch((err) => console.log(err))
    }
  }

  /* "onSubmit" : On excute la fonction "handleRegister" lors de la soumission du formulaire d'inscription */
  // La balise vide est un fragment. Le placement d'un fragment est important lorsqu'on renvoie un component.
  return (
    <>
      {/* Si l'tat "formSubmit" est vrai, alors on renvoie le formulaire de connexion  l'utilisateur pour qu'il se connecte, sachant qu'il vient de crer son compte, sinon on lui envoie le formulaire d'inscription. Ne pas oublier de mettre un fragment, car lorsque l'on fait une condition ternaire, il faut toujours qu'il y ait un lment suprieur aux autres. */}
      {formSubmit ? (
        <>
          <SignInForm />
          <span></span>

          <h4 className="success">
            Enregistrement russi, veuillez vous connecter
          </h4>
        </>
      ) : (
        <form action="" onSubmit={handleRegister} id="sign-up-form">
          {/* Pseudo */}
          <label htmlFor="pseudo">Pseudo</label>
          <br />
          <input
            type="text"
            name="pseudo"
            id="pseudo"
            onChange={(e) => setPseudo(e.target.value)}
            value={pseudo}
          />
          <div className="pseudo error"></div>
          <br />

          {/* Mail */}
          <label htmlFor="email">Courriel</label>
          <br />
          <input
            type="text"
            name="email"
            id="email"
            onChange={(e) => setEmail(e.target.value)}
            value={email}
          />
          <div className="email error"></div>
          <br />

          {/* Mot de passe */}
          <label htmlFor="password">Mot de passe</label>
          <br />
          <input
            type="password"
            name="password"
            id="password"
            onChange={(e) => setPassword(e.target.value)}
            value={password}
          />
          <div className="password error"></div>
          <br />

          {/* Contrle du mot de passe */}
          <label htmlFor="password-conf">Confirmer le mot de passe</label>
          <br />
          <input
            type="password"
            name="password-confirm"
            id="password-confirm"
            onChange={(e) => setControlPassword(e.target.value)}
            value={controlPassword}
          />
          <div className="password-confirm error"></div>
          <br />

          {/* Conditions gnrales d'utilisation */}
          {/* "noopener noreferrer" : Pour des questions de scurit et de performances, il faut les ajouter
           * "noopener" : un attribut pour mieux scuriser les liens raliss en target=blank
           * "noreferrer" : un attribut complmentaire au premier UNIQUEMENT pour Mozilla Firefox mais aussi et SURTOUT un attribut qui empche tout site externe maill dobtenir lorigine du trafic (le referrer).
           */}
          <input type="checkbox" id="terms" />
          <label htmlFor="Terms">
            J'accepte les
            <a href="/" target="blank" rel="noopener noreferrer">
              {' '}
              conditions gnrales d'utilisation
            </a>
          </label>
          <div className="terms error"></div>
          <br />

          {/* Bouton de validation de l'inscription */}
          <input type="submit" value="Valider l'inscription" />
        </form>
      )}
    </>
  )
}

export default SignUpForm
