// Ce fichier sert de reducer pour notre utilisateur connect. On y stocke ses informations pour y avoir accs tout le temps.

import {
  FOLLOW_USER,
  GET_USER,
  UNFOLLOW_USER,
  UPDATE_BIO,
  UPLOAD_PICTURE,
} from '../actions/user.actions'

// Dans ce state, on cherchera en premier lieu les donnes dans la BDD pour la stocker dans le store, pour ne plus jamais avoir  interagir avec la BDD
const initialState = {}

// On export une fonction que l'on appelle "userReducer()", qui sera appele dans le fichier "index.js" du dossier "client/src".
// Cette fonction prend en paramtres le state et une action.

// Notre fonction aura un state que l'on fera voluer en fonction de ce que nous crirons  l'intrieur. On va reprende le state et on le fera voluer.
export default function userReducer(state = initialState, action) {
  // Le type d'action sera le "GET_USER" que nous avons dfini dans le fichier "actions/user.actions.js"
  switch (action.type) {
    case GET_USER:
      // En retournant la data envoye par la BDD, on incrmente notre "initialState", qui est vide au dbut, avec ces donnes, qui seront donc accessibles par tous les components du site.
      return action.payload

    case UPLOAD_PICTURE:
      return {
        // Avec l'oprateur spread, on rcupre les donnes de notre utilisateur sans les craser, sinon il n'y aurait plus que l'image dans les donnes de l'utilisateur. En revanche, on va modifier la donne stocke dans le champ "picture".
        ...state,
        picture: action.payload,
      }

    case UPDATE_BIO:
      return { ...state, bio: action.payload }

    case FOLLOW_USER:
      return {
        ...state,
        following: [action.payload.idToFollow, ...state.following],
      }
    case UNFOLLOW_USER:
      return {
        ...state,
        // En paramtre de la mthode "filter", on dit que tous les ID diffrents de l'ID de la personne  ne plus suivre sont  mettre dans le tableau. On ne retire donc que l'ID de la personne  ne plus suivre.
        following: state.following.filter(
          (id) => id !== action.payload.idToUnfollow
        ),
      }

    // Par dfaut, on retourne le state.
    default:
      return state
  }
}
