// Barre de navigation gauche du site.

import React from 'react'

import { NavLink } from 'react-router-dom'

const LeftNav = () => {
  // Il n'y a que du rendu, aucune logique n'a t code.
  return (
    <div className="left-nav-container">
      <div className="icons">
        {/* Pour englober les icnes */}
        <div className="icons-bis">
          {/* Lorsque l'on se trouve sur le lien du Navlink, la classname "active" est injecte. Elle permet d'ajouter une barre  gauche du bouton contenant le lien mentant  cette page, pour que l'utilisateur visualise bien sur quel bouton il a appuy et / ou sur quelle page il se trouve */}
          {/* Page d'accueil */}
          <NavLink to="/" exact activeClassName="active-left-nav">
            <img src="./img/icons/home.svg" alt="home" />
          </NavLink>
          <br />

          {/* Tendances */}
          <NavLink to="/trending" exact activeClassName="active-left-nav">
            <img src="./img/icons/rocket.svg" alt="trending" />
          </NavLink>
          <br />

          {/* Page de profil */}
          <NavLink to="/profil" exact activeClassName="active-left-nav">
            <img src="./img/icons/user.svg" alt="profile" />
          </NavLink>
          <br />
        </div>
      </div>
    </div>
  )
}

export default LeftNav
