import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { getPosts } from '../actions/post.actions'
import { isEmpty } from './Utils'
import Card from './Post/Card'

const Thread = () => {
  // Par dfaut, on charge les messages.
  const [loadPost, setloadPost] = useState(true)

  // Gestion de l'infinite scroll. Tous les 5 posts, on charge les 5 posts suivants.
  const [count, setCount] = useState(5)

  const dispatch = useDispatch()
  const posts = useSelector((state) => state.postReducer)

  const loadMore = () => {
    // On vrifie si l'utilisateur a atteint le bas de la page.
    // "window.innerHeight + document.documentElement.scrollTop + 1" : l'endroit o l'utilisateur se situe exactement sur la page Web.
    // "document.scrollingElement.scrollHeight" : Tout ce qui est scrollable dans la page.
    if (
      window.innerHeight + document.documentElement.scrollTop + 1 >
      document.scrollingElement.scrollHeight
    ) {
      setloadPost(true)
    }
  }

  // Quand le component "Thread" se lance sur la page d'accueil, il excute la fonction "useEffect" une seule fois. Il vrifie si le modal "loadPost" est sur true, il effectue l'action si c'est le cas, avant de remettre le modal sur "false". Le callback fait que le component est sens relancer la fonction "useEffect()"  chaque fois que le loadPost volue, Cette mthode a surout une utilit prcise quand on utilise un infinite loader.
  useEffect(() => {
    if (loadPost) {
      dispatch(getPosts(count))

      // Une fois notre store rempli avec les posts, on repasse le modal "setLoadPost"  "false" pour ne plus jamais relancer cette action.
      setloadPost(false)
      setCount(count + 5)
    }

    //  chaque fois qu'on atteint le bas de la page, on relance la fonction "useEffect()" pour rajouter les 5 lments suivants, tout a grce au callback.
    // Ds qu'on a excut une fois cette fonction, le modal "loadPost" se remet sur "false", et pour rexcuter cette fonction, on le remet sur "true", et  la fin, il se remet  "false", sinon tous les posts seraient chargs d'un coup.
    // Pour relancer cette fonction, on cre donc un couteur d'vnements (event listener).
    window.addEventListener('scroll', loadMore)

    // Si on veut que cet couteur d'vnements fonctionne, on fait un return en fonction flche.
    // Quand on cre une fonction "useEffect()" et qu'on y ajoute un couteur d'vnements, on met d'abord un vnement qui surveille (ici "window.addEventListener"), puis quand le "useEffect" se dmonte, c'est  dire que l'on n'est plus sur cet lment, on reture cet vnement (ici "window.removeEventListener")
    return () => window.removeEventListener('scroll', loadMore)
  }, [loadPost, dispatch, count])

  return (
    // Fil d'actualit
    <div className="thread-container">
      {/* On va mapper chaque post */}
      {/* Si le tableau contenant la liste des posts n'est pas vide, alors on affiche chaque post du fil d'actualit */}
      <ul>
        {!isEmpty(posts[0]) &&
          posts.map((post) => {
            // On retourne le component "Card" qui va contenir toute la logique ncessaire  l'affichage d'un post.
            // La cl est la cl unique du post.
            return <Card post={post} key={post._id} />
          })}
      </ul>
    </div>
  )
}

export default Thread
