import React from 'react'
import axios from 'axios'

// Quand l'utilisateur se dconnecte, il faut galement supprimer le cookie qu'il reoit, en plus de celui stock sur le serveur, sinon la dconnexion ne russit qu'une fois sur deux. Pour cela, on utilise la librairie "js-cookie"
import cookie from 'js-cookie'

const Logout = () => {
  // La cl  passer en paramtre est la cl du cookie  retirer.
  // Cette fonction est excute en front.
  const removeCookie = (key) => {
    // S'il se passe quelque chose sur la fentre.
    if (window !== 'undefined') {
      // Arguments : cl du cookie, expiration.
      cookie.remove(key, { expires: 1 })
    }
  }

  // Cette fonction est excute en back, sauf la fonction "removeCookie()" que nous avons cr.
  const logout = async () => {
    await axios({
      method: 'get',
      url: `${process.env.REACT_APP_API_URL}api/user/logout`,
      withCredentials: true,
    })
      .then(() => removeCookie('jwt'))
      .catch((err) => console.log(err))

    // Une fois l'utilisateur dconnect, on le renvoie  la page d'accueil.
    window.location = '/'
  }

  return (
    <li onClick={logout}>
      <img src="./img/icons/logout.svg" alt="logout" />
    </li>
  )
}

export default Logout
